.ant-menu-dark .ant-menu-sub {
  background: var(--sidebar);
}

.ant-layout-sider::-webkit-scrollbar {
  display: none;
}
.themeSwitch {
  position: absolute !important;
  bottom: 10px;
  right: 40px;
}
.ant-switch-checked {
  background-color: var(--primary) !important;
}
.ant-switch-handle {
  top: 3px !important;
  width: 15px !important;
  height: 15px !important;
  left: 7px;
}
.ant-switch-checked .ant-switch-inner {
  margin-left: 3px !important;
}
.ant-drawer-content-wrapper {
  width: 100% !important;
}

.side-elements-none{
  display: none;
}

.v-layout .side-elements {
  z-index: 9;
  position: fixed;
  bottom: 0;
  left: 35px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 1.2rem;
  opacity: 0;
  animation: 2s opacityAnimation forwards;
}

.v-layout .side-elements .icon {
  transition: all 0.3s ease;
}

.v-layout .side-elements .icon-one {
  transition: all 0.3s ease;
}

.v-layout .side-elements .icon:hover {
  cursor: pointer;
}

.v-layout .side-elements .icon svg {
  stroke: var(--primary) !important;
  width: 20px;
  height: 20px;
  transition: all 0.3s ease;
}

.v-layout .side-elements .icon:hover svg {
  stroke: var(--customOrange) !important;
}

.v-layout .side-elements .icon-one:hover svg {
  fill: var(--customOrange) !important;
}

.v-layout .side-elements .icon-one svg {
  fill: var(--primary) !important;
}

.v-layout .side-elements .line {
  align-self: center;
  background-color: var(--primary);
  height: 90px;
  width: 1px;
}

.v-layout .right-side-element {
  z-index: 9;
  position: fixed;
  bottom: 0;
  right: -85px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 7rem;
  opacity: 0;
  animation: 2s opacityAnimation forwards;
}

.v-layout .right-side-element .text {
  color: var(--primary);
  font-size: 16px;
  font-weight: 100;
  letter-spacing: 2px;
  transform: rotate(90deg);
  transition: all 0.3s ease;
  margin-bottom: 30px;
}

.v-layout .right-side-element .text:hover {
  cursor: pointer;
  color: var(--customOrange);
}

.v-layout .right-side-element .line {
  align-self: center;
  background-color: var(--primary);
  height: 90px;
  width: 1px;
}

.v-layout .company-logo {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  height: 10vh;
  margin-bottom: 10px;
}
.v-layout .company-logo img {
  width: 70%;
}
.v-layout .company-title {
  font-size: 22px;
  color: var(--text) !important;
  text-transform: uppercase;
}

/* home-icons */

.icon .icon-style a{
  z-index: 3 !important;
  position: absolute;
  width: max-content;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--background);
  border: 1px solid var(--primary);
  border-radius: 50%;
  padding: 1rem;
  transition: all 0.3s ease;
}

.icon .icon-style a:hover {
  cursor: pointer;
  background-color: var(--primary);
}

.icon .icon-style a svg {
  stroke: var(--primary);
  width: 35px;
  height: 35px;
}

.icon .icon-style a:hover svg {
  stroke: var(--background);
}

.icon .icon-style-one {
  transition: all 0.3s ease;
}

.icon .icon-style-one svg {
  fill: var(--primary);
}

.icon .icon-style-one:hover svg {
  fill: var(--background);
}

