.service-section-1-container {
  width: 100%;
  padding: 130px 10% 130px 10%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.service-section-1-container .content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.service-section-1-container .content .title {
  font-size: 75px;
  color: var(--primary);
  font-family: Garamond-Bold !important;
}

.service-section-1-container .content .top {
  display: flex;
  align-items: center;
  column-gap: 1rem;
  background-color: var(--backgroundOne);
  padding: 1.3rem 2.2rem;
  border-radius: 35px;
}

.service-section-1-container .content .top .line-container {
  display: flex;
  align-items: center;
  column-gap: 0.3rem;
}

.service-section-1-container .content .top .line-container .line {
  height: 20px;
  width: 2px;
  background-color: var(--secondary);
}

.service-section-1-container .content .top .left-text-container {
  color: var(--secondary);
  font-size: 18px;
  font-weight: 100;
}

.service-section-1-container .content .top .right-text-container {
  color: var(--primary);
  font-size: 18px;
}

@media (max-width: 991px) {
  .service-section-1-container {
    padding: 90px 10% 90px 10%;
  }

  .service-section-1-container .content .title {
    font-size: 50px;
  }

  .service-section-1-container .content .top .left-text-container {
    font-size: 14px;
  }

  .service-section-1-container .content .top .right-text-container {
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  .service-section-1-container {
    padding: 45px 10% 45px 10%;
  }
  
  .service-section-1-container .content {
    row-gap: 1rem;
  }

  .service-section-1-container .content .title {
    font-size: 40px;
  }
}

