.home-section-4-container {
  width: 100%;
  background-color: var(--backgroundTwo);
  padding: 115px 10%;
  margin: 0 !important;
}

.home-section-4-container .left {
  display: flex;
  align-items: center;
  column-gap: 3rem;
}

.home-section-4-container .middle {
  display: flex;
  align-items: center;
  column-gap: 3rem;
}

.home-section-4-container .right {
  display: flex;
  align-items: center;
  column-gap: 3rem;
}

.home-section-4-container .icon {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--secondary);
  border-radius: 20px;
  padding: 1.3rem;
}

.home-section-4-container .icon svg {
  stroke: var(--primary);
  width: 28px;
  height: 28px;
}

.home-section-4-container .left .icon svg {
  fill: var(--primary);
}

.home-section-4-container .right .icon svg {
  fill: var(--primary);
}

.home-section-4-container .value {
  display: flex;
  flex-direction: column;
  width: 160px;
}

.home-section-4-container .value .number {
  color: var(--primary);
  font-size: 32px !important;
  font-weight: 100;
  display: flex;
  align-items: center;
}

.home-section-4-container .value .number .animated-container div div{
  /* font-size: 52px !important; */
  /* transform: translateY(-10px) !important; */
}

.home-section-4-container .value .title {
  color: var(--secondary);
  font-size: 16px;
}

@media (min-width: 1600px) {
  .home-section-4-container {
    padding: 115px 20%;
  }
}

@media (max-width: 1050px) {
  .home-section-4-container .left {
    column-gap: 1rem;
  }

  .home-section-4-container .middle {
    column-gap: 1rem;
  }

  .home-section-4-container .right {
    column-gap: 1rem;
  }
}

@media(max-width : 992px){
  .home-section-4-container {
    padding: 80px 10%;
  }
}

@media (max-width: 768px) {
  .home-section-4-container {
    padding: 80px 30%;
  }
  .home-section-4-container .left {
    justify-content: space-between;
    column-gap: 2rem;
  }
  .home-section-4-container .middle {
    justify-content: space-between;
    column-gap: 2rem;
  }
  .home-section-4-container .right {
    justify-content: space-between;
    column-gap: 2rem;
  }
  .home-section-4-container .value .number {
    font-size: 30px;
  }

  .home-section-4-container .value .title {
    color: var(--secondary);
    font-size: 16px;
  }
  .home-section-4-container .icon {
    border: 1px solid var(--secondary);
    border-radius: 20px;
    padding: 1.3rem;
  }

  .home-section-4-container .icon svg {
    stroke: var(--primary);
    width: 20px;
    height: 20px;
  }
}

@media (max-width: 600px) {
  .home-section-4-container {
    padding: 80px 25%;
  }
}

@media (max-width: 450px) {
  .home-section-4-container {
    padding: 80px 20%;
  }
}

@media (max-width: 350px) {
  .home-section-4-container .left {
    flex-direction: column;
    align-items: center;
    row-gap: 1rem;
  }
  .home-section-4-container .middle {
    flex-direction: column;
    align-items: center;
    row-gap: 1rem;
  }
  .home-section-4-container .right {
    flex-direction: column;
    align-items: center;
    row-gap: 1rem;
  }
  .home-section-4-container .value {
    align-items: center;
  }
  .home-section-4-container .value .title {
    text-align: center;
  }
}
