.mobile-nav-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 1.5rem 10%;
}

.mobile-nav-container .logo-container {
  /* width: 170px; */
  /* height: 100%; */
  font-size: 40px;
  font-family: Garamond-Bold !important;
  color: var(--secondary);
}

.mobile-nav-container .logo-container img {
  width: 100%;
  height: 100%;
}

.mobile-nav-container .ham-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 0.3rem;
  width: 1.5rem;
  transition: all 0.3s ease;
}

.mobile-nav-container .ham-container:hover {
  cursor: pointer;
}

.mobile-nav-container .ham-container .line {
  width: 100%;
  height: 0.2rem;
  border-radius: 35px;
  background-color: var(--primary);
}

/* ---------------------------------------- Ant Drawer ---------------------------------------- */

.mobile-nav .ant-drawer-content-wrapper {
  width: 400px !important;
}

.mobile-nav
  .ant-drawer-content-wrapper
  .ant-drawer-content
  .ant-drawer-wrapper-body
  .ant-drawer-header {
  background-color: var(--backgroundOne) !important;
  border: none !important;
}

.mobile-nav
  .ant-drawer-content-wrapper
  .ant-drawer-content
  .ant-drawer-wrapper-body
  .ant-drawer-header .ant-drawer-header-title .ant-drawer-close{
    border: 1px solid var(--secondary);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
  }

  .mobile-nav
  .ant-drawer-content-wrapper
  .ant-drawer-content
  .ant-drawer-wrapper-body
  .ant-drawer-header .ant-drawer-header-title .ant-drawer-close svg{
    fill: var(--primary);
  }

.mobile-nav
  .ant-drawer-content-wrapper
  .ant-drawer-content
  .ant-drawer-wrapper-body
  .ant-drawer-body {
    background-color: var(--backgroundOne) !important;
  padding-top: 8rem !important;
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.drawer-container{
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}

.drawer-container .link-outer {
  color: var(--primary);
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 400;
}

.drawer-container .ant-collapse {
  border: none !important;
  color: var(--primary);
  background-color: transparent;
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}

.ant-collapse > .ant-collapse-item > .ant-collapse-header{
    font-size: 22px !important;
}

.drawer-container .ant-collapse .ant-collapse-item {
  border: none !important;
}

.drawer-container .ant-collapse .ant-collapse-item .ant-collapse-content {
  border: none !important;
  background: transparent !important;
}

.drawer-container
  .ant-collapse
  .ant-collapse-item
  .ant-collapse-content
  .ant-collapse-content-box {
  padding: 0 !important;
  padding-left: 0.7rem !important;
  display: flex !important;
  flex-direction: column !important;
  
  row-gap: 0.5rem;
}

.drawer-container
  .ant-collapse
  .ant-collapse-item
  .ant-collapse-content
  .ant-collapse-content-box
  .link {
  color: var(--primary) !important;
  font-size: 16px;
  text-transform: uppercase;
}

.drawer-container .ant-collapse .ant-collapse-item .ant-collapse-header {
  padding: 0 !important;
  flex-direction: row-reverse !important;
  justify-content: space-between !important;
  align-items: center !important;
}

.drawer-container
  .ant-collapse
  .ant-collapse-item
  .ant-collapse-header
  .ant-collapse-header-text {
  font-size: 16px !important;
}

.link-container{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 2rem;
}

.link-container .icon{
  border: 1px solid var(--secondary);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  padding: 1.5rem;
  transition: all 0.3s ease;
}

.link-container .icon:hover{
  cursor: pointer;
  background-color: var(--primary);
  border: 1px solid var(--primary);

}

.link-container .icon svg{
  fill: var(--primary) !important;
  transition: all 0.3s ease;
}

.link-container .icon:hover svg{
  fill: var(--backgroundOne) !important;
}

@media (max-width: 500px) {
  .mobile-nav .ant-drawer-content-wrapper {
    width: 70% !important;
  }
}

@media (max-width: 350px) {
  .mobile-nav-container .logo-container {
    /* width: 130px;
    height: 100%; */
    font-size: 35px;
  }
  .mobile-nav .ant-drawer-content-wrapper {
    width: 100% !important;
  }
}
