.project-detail-section-2-container {
  width: 100%;
  background-color: var(--backgroundOne);
  padding: 115px 10%;
}

.project-detail-section-2-container .img-container {
  margin-bottom: 50px;
  width: 100%;
  height: 600px;
}

.project-detail-section-2-container .img-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-detail-section-2-container .sec-2 .left-col {
  display: flex;
  flex-direction: column;
  row-gap: 3rem;
}

.project-detail-section-2-container .sec-2 .top {
  display: flex;
  align-items: center;
  column-gap: 1rem;
  background-color: var(--backgroundOne);
  padding: 1.3rem 0;
  border-radius: 35px;
}

.project-detail-section-2-container .sec-2 .top .line-container {
  display: flex;
  align-items: center;
  column-gap: 0.3rem;
}

.project-detail-section-2-container .sec-2 .top .line-container .line {
  height: 20px;
  width: 2px;
  background-color: var(--secondary);
}

.project-detail-section-2-container .sec-2 .top .text-container {
  color: var(--secondary);
  font-size: 18px;
  font-weight: 100;
}

.project-detail-section-2-container .sec-2 .left-col .content-1 {
  display: flex;
  flex-direction: column;
  row-gap: 3rem;
}

.project-detail-section-2-container .sec-2 .left-col .content-1 .title {
  color: var(--primary);
  font-size: 55px;
  line-height: 1.02;
  font-family: Garamond-Bold !important;
}

.project-detail-section-2-container .sec-2 .left-col .content-1 .text {
  font-size: 16px;
  color: var(--secondary);
}

.project-detail-section-2-container .sec-2 .left-col .content-2 {
  display: flex;
  flex-direction: column;
  row-gap: 3rem;
}

.project-detail-section-2-container .sec-2 .left-col .content-2 .title {
  color: var(--primary);
  font-size: 45px;
  line-height: 1.02;
  font-family: Garamond-Bold !important;
}

.project-detail-section-2-container .sec-2 .left-col .content-2 .text {
  font-size: 16px;
  color: var(--secondary);
}

.project-detail-section-2-container
  .sec-2
  .left-col
  .content-2
  .content
  .content-row {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  column-gap: 2rem;
}

.project-detail-section-2-container
  .sec-2
  .left-col
  .content-2
  .content
  .content-row
  .icon {
  height: 100%;
  display: flex;
  align-items: center;
  border: 1px solid var(--secondary);
  border-radius: 50%;
  padding: 2rem;
}

.project-detail-section-2-container
  .sec-2
  .left-col
  .content-2
  .content
  .content-row
  .icon
  svg {
  fill: var(--primary) !important;
  width: 30px;
  height: 30px;
}

.project-detail-section-2-container
  .sec-2
  .left-col
  .content-2
  .content
  .content-row
  .title {
  font-size: 28px;
}

.project-detail-section-2-container
  .sec-2
  .left-col
  .content-2
  .content
  .content-row
  .right {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.project-detail-section-2-container .sec-2 .right-col {
  height: 100%;
}

.project-detail-section-2-container .sec-2 .right-col .right-wrapper {
  background-color: var(--background);
  padding: 4rem 3rem;
}

.project-detail-section-2-container .sec-2 .right-col .right-wrapper .title {
  color: var(--primary);
  font-size: 34px;
  font-family: Garamond-Bold !important;
}

.project-detail-section-2-container
  .sec-2
  .right-col
  .right-wrapper
  .box-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
}

.project-detail-section-2-container
  .sec-2
  .right-col
  .right-wrapper
  .box-container
  .box {
  background-color: var(--backgroundOne);
  padding: 2rem 2rem;
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  border: 1px solid var(--backgroundOne);
  transition: all 0.3s ease;
}

.project-detail-section-2-container
  .sec-2
  .right-col
  .right-wrapper
  .box-container
  .box:hover{
      background-color: transparent;
  }

.project-detail-section-2-container
  .sec-2
  .right-col
  .right-wrapper
  .box-container
  .box .box-title{
      color: var(--primary);
      font-size: 15px;
  }

  .project-detail-section-2-container
  .sec-2
  .right-col
  .right-wrapper
  .box-container
  .box .box-content{
      color: var(--secondary);
      font-size: 15px;
  }

@media (max-width: 1200px) {
  .project-detail-section-2-container .img-container {
    height: 500px;
  }
}

@media (max-width: 992px) {
  .project-detail-section-2-container .img-container {
    height: 400px;
  }
}

@media (max-width: 768px) {
  .project-detail-section-2-container .img-container {
    height: 350px;
  }
}
