.sidebar-container {
  width: 100%;
  background-color: var(--background);
  padding: 4rem 2.5rem;
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
}

.sidebar-container .title {
  color: var(--primary);
  font-size: 48px;
  font-family: Garamond-Bold !important;
}

.sidebar-container .btns-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 1.5rem;
}

.sidebar-container .btns-container .btn {
  width: 100%;
  background-color: var(--backgroundOne);
  color: var(--primary);
  font-size: 16px;
  display: flex;
  align-items: center;
  padding: 2rem 2rem;
  /* transition: all 1s ease; */
  position: relative;
}

.sidebar-container .btns-container .btn .arrow-1 {
  position: absolute;
  left: 0;
  opacity: 0;
  display: flex;
  /* transition: all 1s ease; */
}

.sidebar-container .btns-container .btn .arrow-2 {
  position: absolute;
  right: 20px;
  display: flex;
  /* padding-left: 5rem; */
  /* transition: all 1s ease; */
}

.sidebar-container .btns-container .btn .text {
  position: absolute;
  left: 20px;
}

.sidebar-container .btns-container .btn:hover .text {
  animation: textAnimation 0.7s forwards;
}

.sidebar-container .btns-container .btn:hover {
  cursor: pointer;
  background-color: var(--primary);
  font-weight: 100;
  color: var(--background);
}

.sidebar-container .btns-container .btn:hover .arrow-1 {
  animation: leftArrAnimation 0.7s forwards;
}

.sidebar-container .btns-container .btn:hover .arrow-2 {
  animation: rightArrAnimation 0.7s forwards;
}

@keyframes leftArrAnimation {
  0% {
    left: 0px;
    opacity: 0;
  }
  100% {
    left: 30px;
    opacity: 1;
  }
}

@keyframes textAnimation {
  0% {
    left: 20px;
  }
  100% {
    left: 60px;
  }
}

@keyframes rightArrAnimation {
  0% {
    right: 20px;
    opacity: 1;
  }
  100% {
    right: -20px;
    opacity: 0;
  }
}

@media (max-width: 992px) {
  .sidebar-container .title {
    font-size: 40px;
  }
  .sidebar-container .btns-container .btn .text {
    font-weight: 100;
  }
}

@media(max-width : 360px){
  .sidebar-container .btns-container .btn {
    font-size: 13px;
  }
}
