.footer-main-container {
  width: 100%;
  background-color: var(--background);
  /* padding: 0 10%; */
  position: relative;
  overflow: hidden;
}

/*--------------------------------------------------- Top Section Start ---------------------------------------------------*/

.footer-main-container .sec-1 {
  width: 100%;
  border-bottom: 1px solid var(--secondary);
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 2rem;
  padding: 115px 0;
}

/*--------------------------------------------------- Row 1 Start ---------------------------------------------------*/

.footer-main-container .sec-1 .row-1 {
  display: flex;
  column-gap: 1rem;
  align-items: center;
}

.footer-main-container .sec-1 .row-1 .line-container {
  display: flex;
  align-items: center;
  column-gap: 0.2rem;
}

.footer-main-container .sec-1 .row-1 .line-container .line {
  background-color: var(--secondary);
  height: 20px;
  width: 2px;
}

.footer-main-container .sec-1 .row-1 .text-container {
  color: var(--secondary);
  font-size: 18px;
}

/*--------------------------------------------------- Row 1 End ---------------------------------------------------*/

/*--------------------------------------------------- Row 2 Start ---------------------------------------------------*/

.footer-main-container .sec-1 .row-2 {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.footer-main-container .sec-1 .row-2 .title {
  font-size: 70px;
  color: var(--primary);
  font-family: Garamond-Bold !important;
  text-align: center;
}

.footer-main-container .sec-1 .row-2 .search-subscribe {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  row-gap: 2rem;
  column-gap: 2rem;
}

.footer-main-container .sec-1 .row-2 .search-subscribe .ant-input {
  border-radius: 35px !important;
  border: 0.5px solid var(--secondary) !important;
  padding: 2rem;
  width: 600px;
}

.footer-main-container .sec-1 .row-2 .search-subscribe .btn-container {
  min-width: max-content;
  max-width: max-content;
  background-color: var(--secondary) !important;
  color: var(--primary);
  font-size: 16px;
  padding: 1.3rem 2rem;
  border-radius: 35px;
  transition: all 0.3s ease;
}

.footer-main-container .sec-1 .row-2 .search-subscribe .btn-container:hover {
  cursor: pointer;
}

/*--------------------------------------------------- Row 2 End ---------------------------------------------------*/

/*--------------------------------------------------- Row 3 Start ---------------------------------------------------*/

.footer-main-container .sec-1 .row-3 {
  display: flex;
  align-items: center;
  column-gap: 2rem;
  row-gap: 2rem;
  padding-top: 100px;
  flex-wrap: wrap;
}

.footer-main-container .sec-1 .row-3 .contact {
  display: flex;
  align-items: center;
  column-gap: 2rem;
}

.footer-main-container .icon {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  border: 1px solid var(--secondary);
  border-radius: 50%;
  transition: all 0.3s ease;
}

.footer-main-container .icon:hover {
  cursor: pointer;
  background-color: var(--primary);
}

.footer-main-container .icon-one a{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  border: 1px solid var(--secondary);
  border-radius: 50%;
  transition: all 0.3s ease;
}

.footer-main-container .icon-one a:hover {
  cursor: pointer;
  background-color: var(--primary);
}

.footer-main-container .icon svg {
  fill: var(--primary) !important;
  transition: all 0.3s ease;
}

.footer-main-container .icon:hover svg {
  fill: var(--background) !important;
}

.footer-main-container .icon-one svg {
  stroke: 1px solid var(--primary);
  fill: var(--primary) !important;
  transition: all 0.3s ease;
}

.footer-main-container .icon-one a:hover svg {
  fill: var(--background) !important;
}

.footer-main-container .sec-1 .row-3 .contact p {
  color: var(--primary);
  font-size: 26px;
}

.footer-main-container .sec-1 .row-3 .icons-container {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

/*--------------------------------------------------- Row 3 Start ---------------------------------------------------*/

/*--------------------------------------------------- Top Section End ---------------------------------------------------*/

/*--------------------------------------------------- Bottom Section Start ---------------------------------------------------*/

.footer-main-container .sec-2 {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 2rem 10%;
}

.footer-main-container .sec-2 .text-container {
  color: var(--secondary);
  font-size: 16px;
}

.footer-main-container .sec-2 .text-container span {
  color: var(--primary);
}

/* .footer-main-container .sec-2 .logo-container img {
  width: 100%;
  height: 100%;
} */

/* .footer-main-container .sec-2 .logo-container {
  width: 170px;
  height: 100%;
} */

.footer-main-container .sec-2 .logo-container{
  font-size: 40px;
  font-family: Garamond-Bold !important;
  color: var(--secondary);
  margin-right: 18px;
}

/*--------------------------------------------------- Bottom Section End ---------------------------------------------------*/

/* --------------------------------------- Shape ---------------------------------------*/

/* left */
.footer-main-container .left-shape {
  position: absolute;
  top: -90px;
  left: 0;
  z-index: 1;
}

.footer-main-container .left-shape .inner-most {
  width: 440px;
  height: 440px;
}

.footer-main-container .left-shape .inner {
  width: 560px;
  height: 560px;
}

.footer-main-container .left-shape .outer {
  width: 700px;
  height: 700px;
}

.footer-main-container .left-shape span {
  position: absolute;
  border: 1px solid var(--secondary);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  visibility: visible;
  display: block;
  top: 50%;
  left: 50%;
  opacity: 1;
}

/* right */

.footer-main-container .right-shape {
  position: absolute;
  top: -90px;
  right: 0;
  z-index: 1;
}

.footer-main-container .right-shape .inner-most {
  width: 440px;
  height: 440px;
}

.footer-main-container .right-shape .inner {
  width: 560px;
  height: 560px;
}

.footer-main-container .right-shape .outer {
  width: 700px;
  height: 700px;
}

.footer-main-container .right-shape span {
  position: absolute;
  border: 1px solid var(--secondary);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  visibility: visible;
  display: block;
  top: 50%;
  left: 50%;
  opacity: 1;
}

/* --------------------------------------- Shape ---------------------------------------*/

@media (max-width: 1200px) {
  .footer-main-container .left-shape {
    display: none;
  }

  .footer-main-container .right-shape {
    display: none;
  }
  .footer-main-container .sec-2{
    justify-content: center;
  }
  .footer-main-container .sec-1 .row-3 {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer-main-container .sec-1 .row-3 .icons-container{
    margin: 0 auto;
  }
  
}

@media (max-width: 992px) {
  .footer-main-container .sec-1 {
    padding: 90px 20px;
  }
}

@media (max-width: 950px) {
  .footer-main-container .sec-1 .row-2 .search-subscribe {
    justify-content: center;
  }

  .footer-main-container .sec-1 .row-3 {
    justify-content: center;
  }
}

@media (max-width: 826px) {
  .footer-main-container .sec-1 .row-2 .title {
    font-size: 50px;
  }
  

  .footer-main-container .sec-1 .row-2 .search-subscribe .ant-input {
    width: 100%;
  }

  .footer-main-container .sec-1 .row-3 {
    padding-top: 50px;
  }
}

@media (max-width: 602px) {
  .footer-main-container .sec-1 {
    padding: 75px 20px;
  }
  .footer-main-container .sec-1 .row-2 {
    align-items: center;
  }
  .footer-main-container .sec-1 .row-2 .title {
    font-size: 30px;
  }
  .footer-main-container .sec-1 .row-3 .contact p {
    font-size: 18px;
  }
  .footer-main-container .icon {
    padding: 1rem;
  }
}

@media (max-width: 380px) {
  .footer-main-container .sec-1 .row-2 .title {
    font-size: 23px;
  }

  .footer-main-container .sec-1 .row-2 .search-subscribe .btn-container {
    font-size: 14px;
    padding: 1rem 1.5rem;
  }
  .footer-main-container .sec-1 .row-3 .contact p {
    font-size: 15px;
  }
  .footer-main-container .icon {
    padding: 1rem;
  }
}
