.plan-card-container {
  width: max-content;
  background-color: var(--background);
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 3rem;
  padding: 4rem;
}

.plan-card-container .top {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.plan-card-container .top .title {
  font-size: 30px !important;
  font-family: Garamond-Bold !important;
  color: var(--primary);
}

.plan-card-container .top .amount {
  font-size: 58px !important;
  color: var(--primary);
}

.plan-card-container .middle {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 1rem;
}

.plan-card-container .middle .services {
  color: var(--secondary);
  font-size: 16px;
}

.plan-card-container .bottom .btn-container {
  width: max-content;
  font-size: 15px;
  color: var(--primary);
  border: 1px solid var(--primary);
  border-radius: 35px;
  padding: 1rem 3.5rem;
  transition: all 0.3s ease;
}

.plan-card-container .bottom .btn-container:hover {
  cursor: pointer;
  background-color: var(--backgroundOne);
  border: 1px solid var(--backgroundOne);
}

@media (max-width: 425px) {
  .plan-card-container {
    width: 100%;
    padding: 2rem;
  }
  .plan-card-container .top .amount {
    font-size: 46px !important;
  }
}

@media (max-width: 320px) {
  .plan-card-container .bottom .btn-container {
    font-size: 13px;
    padding: 1rem 2.5rem;
  }
  .plan-card-container {
    padding: 2rem 1rem;
  }
}
