.home-section-1-container {
  width: 100%;
  padding: 115px 10% 0 10%;
  display: flex;
  position: relative;
  overflow: hidden;
}

.home-section-1-container .left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  row-gap: 3rem;
  z-index: 3;
  margin-bottom: 4rem;
}

/*---------------------------------------- Test ----------------------------------------*/
.home-section-1-container .left .title-container {
  /* width: 250px; */
  /* height: 250px; */
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  row-gap: 2rem;
}
.home-section-1-container .left .title-container .title-1 {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  height: 50px;
}
.home-section-1-container .left .title-container .title-1 .block {
  width: 0%;
  height: inherit;
  /* background: #ffb510; */
  background: var(--primary);
  position: absolute;
  animation: mainBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  display: flex;
}

.home-section-1-container .left .title-container .title-1 .title-1-text {
  font-size: 60px;
  /* line-height: 60px; */
  color: var(--primary);
  font-family: Garamond-Bold !important;
  -webkit-animation: mainFadeIn 2s forwards;
  -o-animation: mainFadeIn 2s forwards;
  animation: mainFadeIn 2s forwards;
  animation-delay: 1.6s;
  opacity: 0;
  display: flex;
  /* align-items: baseline; */
  position: relative;
}

.home-section-1-container .left .title-container .title-2 {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  height: 80px;
  margin-top: -10px;
}
.home-section-1-container .left .title-container .title-2 .block {
  width: 0%;
  height: inherit;
  /* background: #e91e63; */
  background: var(--primary);
  position: absolute;
  animation: secBlock 2s cubic-bezier(0.74, 0.06, 0.4, 0.92) forwards;
  animation-delay: 2s;
  display: flex;
}

.home-section-1-container .left .title-container .title-2 span {
  font-size: 80px;
  line-height: 80px;
  color: var(--primary);
  font-family: Garamond-Bold !important;
  position: relative;
  animation: secFadeIn 2s forwards;
  animation-delay: 3.2s;
  opacity: 0;
}

