.home-section-1-container .left .desc {
  color: var(--secondary);
  font-size: 18px;
  position: relative;
}

.home-section-1-container .left .desc span {
  position: relative;
  display: inline-block;
  animation: flip 4s infinite;
  animation-delay: calc(0.1s * var(--i));
}

.home-section-1-container .left .btn-container {
  width: max-content;
  color: var(--primary);
  font-size: 15px;
  border: 1px solid var(--secondary);
  border-radius: 35px;
  padding: 1.5rem 3.5rem;
  transition: all 0.3s ease;
  margin-bottom: 200px;
}

.home-section-1-container .left .btn-container:hover {
  cursor: pointer;
  background-color: var(--backgroundOne);
  border: 1px solid var(--backgroundOne);
}

.home-section-1-container .left .video-btn-container {
  margin-top: 4rem;
  display: flex;
  align-items: center;
  column-gap: 3rem;
  z-index: 3;
}

.home-section-1-container .left .video-btn-container .video-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  /* border: 1px solid var(--secondary); */
  padding: 35px;
  transition: all 0.3s ease;
  background-color: var(--backgroundOne);
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
  /* transform: scale(1); */
  animation: pulse 2s infinite;
}

.home-section-1-container .left .video-btn-container .video-btn:hover {
  cursor: pointer;
}

.home-section-1-container .left .video-btn-container .video-btn svg {
  fill: var(--secondary);
  width: 25px;
  height: 25px;
  transition: all 0.3s ease;
}

.home-section-1-container .left .video-btn-container .video-btn:hover svg {
  fill: var(--primary);
}

.home-section-1-container .left .video-btn-container p {
  color: var(--secondary);
  font-size: 16px;
}

.home-section-1-container .right {
  display: none;
  max-height: 700px;
  z-index: 3;
}

.home-section-1-container .right img {
  max-width: 100%;
  height: auto;
}

/* --------------------------------------- Shape ---------------------------------------*/

/* Top */
.home-section-1-container .top-shape {
  position: absolute;
  top: -90px;
  left: 0;
  z-index: 1;
}

.home-section-1-container .top-shape .inner-most {
  width: 440px;
  height: 440px;
}

.home-section-1-container .top-shape .inner {
  width: 560px;
  height: 560px;
}

.home-section-1-container .top-shape .outer {
  width: 700px;
  height: 700px;
}

.home-section-1-container .top-shape span {
  position: absolute;
  border: 1px solid var(--secondary);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  visibility: visible;
  display: block;
  top: 50%;
  left: 50%;
  opacity: 1;
}

