.home-section-2-container {
  width: 100%;
  background-color: var(--backgroundOne);
  padding: 115px 10%;
  position: relative;
  z-index: 3;
}

.home-section-2-container .sec-1 {
  padding-bottom: 4rem;
}

.home-section-2-container .sec-1 .top {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

.home-section-2-container .sec-1 .top .line-container {
  display: flex;
  align-items: center;
  column-gap: 0.3rem;
}

.home-section-2-container .sec-1 .top .line-container .line {
  height: 20px;
  width: 2px;
  background-color: var(--secondary);
}

.home-section-2-container .sec-1 .top .text-container {
  color: var(--secondary);
  font-size: 18px;
}

.home-section-2-container .sec-1 .title {
  font-size: 60px;
  color: var(--primary);
  font-family: Garamond-Bold !important;
}

.ant-carousel .service-carousel{
  display: flex !important;
  flex-direction: column !important;
  row-gap: 2rem;
}

.ant-carousel .service-carousel .slick-list .slick-track {
  height: max-content !important;
}

.ant-carousel
  .service-carousel
  .slick-list
  .slick-track
  .slick-slide
  div
  .service-wrappers-container {
  width: 100%;
  display: flex !important;
  column-gap: 3rem;
}

.ant-carousel
  .service-carousel
  .slick-list
  .slick-track
  .slick-slide
  div
  .service-wrappers-container
  .service-wrapper {
  flex: 1;
}

.ant-carousel .service-carousel .slick-dots{
  position: static !important;
}

.ant-carousel .service-carousel .slick-dots li button{
  border-radius: 50% !important;
  width: 10px !important;
  height: 10px !important;
}

@media (min-width: 1600px) {
  .home-section-2-container {
    padding: 115px 20%;
  }
}

@media(max-width : 992px){
  .home-section-2-container {
    padding: 80px 10%;
  }
  .home-section-2-container .sec-1 .title {
    font-size: 50px;
  }
}

@media (max-width: 900px) {
  .ant-carousel
    .service-carousel
    .slick-list
    .slick-track
    .slick-slide
    div
    .service-wrappers-container {
    flex-direction: column;
    align-items: center;
    row-gap: 3rem;
  }

  .ant-carousel
    .service-carousel
    .slick-list
    .slick-track
    .slick-slide
    div
    .service-wrappers-container
    .service-wrapper {
    max-width: 450px !important;
    min-width: 450px !important;
  }
}

@media(max-width : 768px){
  .home-section-2-container .sec-1 .title {
    font-size: 40px;
  }
}

@media(max-width : 575px){
  .home-section-2-container .sec-1 .title {
    font-size: 36px;
  }
}

@media(max-width : 480px){
    .ant-carousel
    .service-carousel
    .slick-list
    .slick-track
    .slick-slide
    div
    .service-wrappers-container
    .service-wrapper {
    max-width: 100% !important;
    min-width: 100% !important;
    /* width: 100%; */
  }
}
