.home-section-8-container {
  width: 100%;
  background-color: var(--backgroundOne);
  padding: 115px 10%;
  margin: 0 !important;
  position: relative;
  z-index: 3;
}

.home-section-8-container .sec-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 2rem;
}

.home-section-8-container .sec-1 .top {
  display: flex;
  align-items: center;
  column-gap: 2rem;
}

.home-section-8-container .sec-1 .top .line-container {
  display: flex;
  align-items: center;
  column-gap: 0.3rem;
}

.home-section-8-container .sec-1 .top .line-container .line {
  height: 20px;
  width: 2px;
  background-color: var(--secondary);
}

.home-section-8-container .sec-1 .top .text-container {
  color: var(--secondary);
  font-size: 18px;
}

.home-section-8-container .sec-1 .title {
  font-size: 60px;
  color: var(--primary);
  font-family: Garamond-Bold !important;
}

@media (min-width: 1600px) {
  .home-section-8-container {
    padding: 115px 20%;
  }
}

@media (max-width: 992px) {
  .home-section-8-container {
    padding: 80px 10%;
  }
  .home-section-8-container .sec-1 .title {
    font-size: 50px;
  }
}

@media (max-width: 768px) {
  .home-section-8-container .sec-1 .title {
    font-size: 40px;
  }
}

@media (max-width: 575px) {
  .home-section-8-container .sec-1 .title {
    font-size: 36px;
  }
}

@media (max-width: 500px) {
  .home-section-8-container .sec-1 .title {
    font-size: 30px;
  }
}
