.mainContent {
  background-color: var(--background);
  /* min-height: 88vh; */
  /* padding: 0.6rem 1.4rem; */
}
.ant-layout-header {
  background-color: var(--background) !important;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  z-index: 6;
  /* padding: 0 30px; */
  padding: 0 !important;
}

.v-layout .ant-layout{
  background-color: var(--backgroundOne) !important;
}

.v-layout {
  display: block;
}
.m-layout {
  display: none;
}
@media (max-width: 992px) {
  .v-layout {
    display: none;
  }
  .m-layout {
    display: block;
  }
}
