.thumbnail-container {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.thumbnail-container .content-container .title {
  font-size: 28px;
  color: var(--primary);
  transition: all 0.3s ease;
}

.thumbnail-container .content-container .title:hover {
  cursor: pointer;
}

.thumbnail-container .img-container {
  overflow: hidden;
  padding: 30px 30px 0 30px;
  height: 300px;
  position: relative;
  z-index: 1;
}

.thumbnail-container .img-container .bg-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(37, 39, 52, 0.95);
}

.thumbnail-container .img-container .bg-image {
  position: absolute;
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
  transform: translate(-50%, -50%) rotate(-45deg);
  top: 50%;
  left: 50%;
  z-index: -1;
}

.thumbnail-container .img-container .bg-image img {
  max-width: 150%;
}

.thumbnail-container .img-container .image {
  position: relative;
  z-index: 1;

  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000px;
}

.thumbnail-container .img-container:hover .image {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}

.thumbnail-container .img-container .image img {
  width: 100%;
}

@keyframes shake {
  10%,
  90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%,
  60% {
    transform: translate3d(4px, 0, 0);
  }
}

/* ---------------------------------------- media queries ---------------------------------------- */

@media (min-width: 1400px) {
  .thumbnail-container .img-container {
    padding: 100px 100px 0 100px;
    height: 548px;
  }
}

@media (min-width: 1200px) {
  .thumbnail-container .img-container {
    padding: 100px 100px 0 100px;
    height: 450px;
  }
}

@media (max-width: 768px) {
  .thumbnail-container .content-container .title {
    font-size: 22px;
  }
}
