.contact-section-2-container {
  background-color: var(--backgroundOne);
  width: 100%;
  padding: 115px 10%;
}

.contact-section-2-container .contact-section-2-wrapper {
  width: 100%;
  margin: 0 !important;
}

.contact-section-2-container .contact-section-2-wrapper .right-col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 2rem;
}

.contact-section-2-container .contact-section-2-wrapper .right-col .top {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .top
  .line-container {
  display: flex;
  align-items: center;
  column-gap: 0.3rem;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .top
  .line-container
  .line {
  height: 20px;
  width: 2px;
  background-color: var(--secondary);
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .top
  .text-container {
  color: var(--secondary);
  font-size: 18px;
  font-weight: 100;
}

.contact-section-2-container .contact-section-2-wrapper .right-col .title {
  font-size: 66px;
  font-family: Garamond-Bold !important;
  color: var(--primary);
  line-height: 1.02;
}

.contact-section-2-container .contact-section-2-wrapper .right-col .content {
  color: var(--secondary);
  font-size: 16px;
  margin-bottom: 1rem;
}

.contact-section-2-container .contact-section-2-wrapper .left-col {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.contact-section-2-container .contact-section-2-wrapper .left-col .top {
  width: 100%;
}

.contact-section-2-container .contact-section-2-wrapper .left-col .bottom {
  width: 100%;
  background-color: var(--background);
  padding: 3.5rem 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 2rem;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .left-col
  .bottom
  .title {
  font-size: 48px;
  font-family: Garamond-Bold !important;
  color: var(--primary);
}

.contact-section-2-container
  .contact-section-2-wrapper
  .left-col
  .bottom
  .links {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .left-col
  .bottom
  .links
  .icon a{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  border: 1px solid var(--secondary);
  border-radius: 50%;
  transition: all 0.3s ease;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .left-col
  .bottom
  .links
  .icon a:hover {
  cursor: pointer;
  background-color: var(--primary);
}

.contact-section-2-container
  .contact-section-2-wrapper
  .left-col
  .bottom
  .links
  .icon a
  svg {
  stroke: 1px solid var(--secondary);
    fill: var(--primary) !important;
  transition: all 0.3s ease;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .left-col
  .bottom
  .links
  .icon a:hover
  svg {
  fill: var(--background) !important;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .form-container
  input {
  outline: 1px solid var(--secondary);
  background-color: transparent;
  padding: 1rem;
  width: 50%;
  border: none !important;
  color: var(--primary);
  transition: all 0.3s ease;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .form-container
  input:focus {
  outline: 1px solid var(--primary);
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .form-container
  textarea {
  outline: 1px solid var(--secondary);
  background-color: transparent;
  padding: 1rem;
  width: 50%;
  border: none !important;
  color: var(--primary);
  transition: all 0.3s ease;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .form-container
  textarea:focus {
  outline: 1px solid var(--primary);
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .form-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .form-container
  .top {
  width: 100%;
  display: flex;
  column-gap: 1rem;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .form-container
  .middle {
  width: 100%;
  display: flex;
  column-gap: 1rem;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .form-container
  .bottom
  textarea {
  width: 100% !important;
  height: 10rem;
}

@media (min-width: 1800px) {
  .contact-section-2-container
    .contact-section-2-wrapper
    .right-col
    .form-container
    input {
    padding: 2rem 1rem;
  }
}

@media (max-width: 1200px) {
  .contact-section-2-container .contact-section-2-wrapper .right-col .title {
    font-size: 55px;
    line-height: 1.02;
  }
}

@media (max-width: 991px) {
  .contact-section-2-container .contact-section-2-wrapper .left-col .bottom {
    align-items: center;
  }
  .contact-section-2-container .contact-section-2-wrapper .right-col .title {
    font-size: 48px;
    line-height: 1.02;
  }
  .contact-section-2-container
    .contact-section-2-wrapper
    .left-col
    .bottom
    .title {
    font-size: 40px;
  }
}

@media (max-width: 768px) {
  .contact-section-2-container .contact-section-2-wrapper .right-col .title {
    font-size: 38px;
    line-height: 1.02;
  }
}

@media (max-width: 575px) {
  .contact-section-2-container .contact-section-2-wrapper .right-col .title {
    font-size: 34px;
    line-height: 1.02;
  }
}

@media (max-width: 520px) {
  .contact-section-2-container
    .contact-section-2-wrapper
    .right-col
    .form-container
    input {
    width: 100%;
  }

  .contact-section-2-container
    .contact-section-2-wrapper
    .right-col
    .form-container
    .top {
    flex-direction: column;
    row-gap: 2rem;
  }

  .contact-section-2-container
    .contact-section-2-wrapper
    .right-col
    .form-container
    .middle {
    flex-direction: column;
    row-gap: 2rem;
  }
}

.contact-section-2-container .contact-section-2-wrapper .right-col .submit-btn {
  width: max-content;
  color: var(--primary);
  font-size: 18px;
  border: 1px solid var(--secondary);
  padding: 1rem 2rem;
  border-radius: 30px;
  transition: all 0.3s ease;
}

.contact-section-2-container
  .contact-section-2-wrapper
  .right-col
  .submit-btn:hover {
  cursor: pointer;
  /* background-color: var(--backgroundOne); */
  /* border: 1px solid var(--backgroundOne); */
}

@media (max-width: 500px) {
  .contact-section-2-container {
    padding: 80px 5%;
  }
}

@media (max-width: 380px) {
  .contact-section-2-container {
    padding: 80px 2%;
  }
}
