.contact-card-container {
  width: 100%;
  background-color: var(--background);
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  padding: 5rem 3.5rem;
}

.contact-card-container .contact-info-container {
  width: 100%;
  background-color: var(--backgroundOne);
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  border: 1px solid var(--backgroundOne);
  padding: 2rem;
  transition: all 0.3s ease;
}

.contact-card-container .contact-info-container:hover {
  background-color: var(--background);
}

.contact-card-container .contact-info-container .title {
  width: 100%;
  color: var(--primary);
  font-size: 15px;
}

.contact-card-container .contact-info-container .info {
  width: 100%;
  color: var(--secondary);
  font-size: 16px;
}

@media (max-width: 500px) {
  .contact-card-container {
    padding: 3rem 2rem;
  }
}

@media(max-width : 400px){
    .contact-card-container .contact-info-container{
        padding: 2rem 1rem;
    }
}

