.home-section-7-container {
  background-color: var(--backgroundOne);
  padding: 115px 10%;
}

.home-section-7-container .sec-1 {
  padding-bottom: 4rem;
}

.home-section-7-container .sec-1 .top {
  display: flex;
  align-items: center;
  column-gap: 2rem;
}

.home-section-7-container .sec-1 .top .line-container {
  display: flex;
  align-items: center;
  column-gap: 0.3rem;
}

.home-section-7-container .sec-1 .top .line-container .line {
  height: 20px;
  width: 2px;
  background-color: var(--secondary);
}

.home-section-7-container .sec-1 .top .text-container {
  color: var(--secondary);
  font-size: 18px;
}

.home-section-7-container .sec-1 .title {
  font-size: 60px;
  color: var(--primary);
  font-family: Garamond-Bold !important;
}

.home-section-7-container .sec-2{
  margin: 0 !important;
}

.home-section-7-container .sec-2 .col-container {
  width: 100%;
  display: flex;
  justify-content: center;
}

.home-section-7-container .sec-2 .img-container {
  min-width: 267px;
  max-width: 267px;
  height: 250px;
  border: 1px solid var(--secondary);
  padding: 50px;
  transition: all 0.3s ease;
}

.home-section-7-container .sec-2 .img-container:hover {
  cursor: pointer;
  background-color: var(--background);
  border: 1px solid var(--background);
}

.home-section-7-container .sec-2 .img-container img {
  width: 100%;
  height: 100%;
  fill: var(--secondary);
}

@media (min-width: 1600px) {
  .home-section-7-container {
    padding: 115px 20%;
  }
}

@media(max-width : 992px){
  .home-section-7-container {
    padding: 80px 10%;
  }
  .home-section-7-container .sec-1 .title {
    font-size: 50px;
  }
}

@media(max-width: 768px){
  .home-section-7-container .sec-1 .title {
    font-size: 40px;
  }
}

@media (max-width: 575px) {
  .home-section-7-container .sec-1 .title {
    font-size: 36px;
  }
}

@media (max-width: 500px) {
  .home-section-7-container .sec-1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 1rem;
    padding-bottom: 2rem;
  }

  .home-section-7-container .sec-1 .title {
    text-align: center;
    font-size: 30px;
  }
}

@media (max-width: 375px) {
  .home-section-7-container .sec-2 .img-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 100%;
    max-width: 100%;
    height: 100%;
    padding: 50px;
  }
  .home-section-7-container .sec-2 .img-container img {
    width: 100%;
    height: 100%;
  }
}
