.nav-main-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  padding: 1rem 10%;
}

.nav-main-container .logo-container{
  font-size: 40px;
  font-family: Garamond-Bold !important;
  color: var(--secondary);
}

/* .nav-main-container .logo-container {
  width: 170px;
  height: 100%;
} */

/* .nav-main-container .logo-container img {
  width: 100%;
  height: 100%;
} */

.nav-main-container ul {
  /* height: 100%; */
  min-width: 30rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style-type: none;
  margin: 0 !important;
  padding: 0 !important;
}

.nav-main-container ul li {
  width: 100%;
  height: 100%;
  transition: all 0.4s ease;
  position: relative;
}

.nav-main-container ul li .non-drop-img {
  position: absolute;
  opacity: 0;
  transition: all 0.8s ease;
}

.nav-main-container ul li:hover {
  cursor: pointer;
}

.nav-main-container ul li:hover .non-drop-img {
  opacity: 1;
}

.nav-main-container ul li a {
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  color: var(--secondary) !important;
  font-size: 18px;
}

.drop-down-custom-inner{
  width: 100%;
  height: 100%;
  position: relative;
}

/* .drop-down-custom-inner img{
  position: absolute;
  opacity: 1;
} */

.nav-main-container ul li:hover a {
  cursor: pointer;
  color: var(--primary) !important;
}

/*  */

.ant-dropdown .ant-dropdown-menu .ant-dropdown-menu-item {
  --initialTextColor: #FFF;
  --slideTextColor: #272838;

  --initialBgColor: #272838;
  --slideBgColor: #FFF;

  color: var(--initialTextColor);

  background-image: linear-gradient(90deg,
      var(--initialBgColor) 0%,
      var(--initialBgColor) 50%,
      var(--slideBgColor) 50%,
      var(--slideBgColor) 100%);
  background-size: 300%;
  transition: background-position .3s cubic-bezier(.47, .1, 1, .63),
    color .2s linear;
  transition-delay: 0.0s, 0.15s;
}

.ant-dropdown .ant-dropdown-menu .ant-dropdown-menu-item:hover {
  color: var(--slideTextColor);
  cursor: pointer;
  background-position: -50% 100%;
}

/*  */

.ant-dropdown .ant-dropdown-menu {
  margin-top: 1rem;
  background: transparent !important;
  background-color: rgba(37, 39, 52, 0.7) !important;
  padding: 0 !important;
  min-width: 19rem;
}

.ant-dropdown .ant-dropdown-menu .ant-dropdown-menu-item {
  padding: 1rem;
  color: var(--primary);
  font-size: 16px;
  letter-spacing: 1px;
  transition: all 0.3s ease !important;
}

.ant-dropdown .ant-dropdown-menu .ant-dropdown-menu-item:hover {
  color: black !important;
}
