.blog-post-container {
  background-color: var(--background);
  padding: 50px;
  display: flex;
  column-gap: 4rem;
}

.blog-post-container .left .img-container {
  width: 180px;
  height: 180px;
}

.blog-post-container .left .img-container img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.blog-post-container .right {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.blog-post-container .right .title-date {
  color: var(--primary);
  font-size: 16px;
}

.blog-post-container .right .title-date span {
  color: var(--secondary);
}

.blog-post-container .right .desc {
  font-size: 25px;
  color: var(--primary);
}

.blog-post-container .right .come-soon{
  font-size: 18px;
  color: var(--secondary);
}

.blog-post-container .right .btn-container {
  width: max-content;
  color: var(--primary);
  font-size: 18px;
  border: 1px solid var(--secondary);
  padding: 1rem 2rem;
  border-radius: 30px;
  transition: all 0.3s ease;
}

.blog-post-container .right .btn-container:hover {
  cursor: pointer;
  background-color: var(--backgroundOne);
  border: 1px solid var(--backgroundOne);
}

@media (max-width: 768px) {
  .blog-post-container {
    flex-direction: column;
    row-gap: 2rem;
  }
  .blog-post-container .left .img-container {
    width: 100%;
    height: 100%;
  }
}

@media (max-width: 600px) {
  .blog-post-container .right .desc {
    font-size: 20px;
  }
  .blog-post-container .right .btn-container {
    font-size: 14px;
    padding: 1rem 2rem;
  }
}

@media (max-width: 450px) {
  .blog-post-container {
    padding: 30px;
  }
  .blog-post-container .right .title-date {
    font-size: 14px;
  }
  .blog-post-container .right .desc {
    font-size: 18px;
  }
  .blog-post-container .right .btn-container{
    align-self: center;
  }
}

@media(max-width : 375px){
  .blog-post-container .right .desc {
    font-size: 16px;
  }
  .blog-post-container .right .btn-container {
    font-size: 13px;
    padding: 0.8rem 1.8rem;
  }
}
