.progress-container {
  background-color: var(--backgroundOne) !important;
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  padding: 3rem 2.5rem;
}

.progress-container .top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.progress-container .top .title {
  color: var(--secondary);
  font-size: 16px;
}

.progress-container .top .number {
  color: var(--secondary);
  font-size: 16px;
}

.progress-container .ant-progress .ant-progress-outer .ant-progress-inner {
  border-radius: 0 !important;
  background-color: var(--secondary) !important;
  height: 5px;
}

.progress-container
  .ant-progress
  .ant-progress-outer
  .ant-progress-inner
  .ant-progress-bg {
  border-radius: 0 !important;
  background-color: var(--primary) !important;
}

