.service-container {
  width: 100%;
  background-color: var(--background);
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  padding: 70px 50px;
  position: relative;
  overflow: hidden;
}

.service-container .icon-container svg {
  fill: var(--primary);
  width: 60px;
  height: 60px;
}

.service-container .title {
  color: var(--primary);
  font-size: 28px;
}

.service-container ul {
  list-style-type: none;
  margin: 0 !important;
  padding: 0 !important;
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  z-index: 2;
}

.service-container ul li {
  color: var(--secondary);
  display: flex;
  align-items: center;
  column-gap: 1rem;
  font-size: 18px;
}

/* ------------------------------------------ Shape ------------------------------------------*/

.service-container .top-shape {
  position: absolute;
  /* top: -100px;
    right: -20px; */
  top: -150px;
  right: -70px;
  z-index: 1;
  opacity: 0;
}

.service-container:hover .top-shape {
  animation: 1s hoverEffectTop forwards;
}

.service-container .top-shape .inner-most {
  width: 400px;
  height: 400px;
}

.service-container .top-shape .inner {
  width: 500px;
  height: 500px;
}

.service-container .top-shape .outer {
  width: 600px;
  height: 600px;
}

.service-container .top-shape span {
  position: absolute;
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  visibility: visible;
  display: block;
  top: 50%;
  left: 50%;
  opacity: 1;
}

.service-container .bottom-shape {
  position: absolute;
  /* bottom: -100px;
  right: -20px; */
  bottom: -150px;
  right: -70px;
  z-index: 1;
  opacity: 0;
}

.service-container:hover .bottom-shape {
    animation: 1s hoverEffectBottom forwards;
  }

.service-container .bottom-shape .inner-most {
  width: 400px;
  height: 400px;
}

.service-container .bottom-shape .inner {
  width: 500px;
  height: 500px;
}

.service-container .bottom-shape .outer {
  width: 600px;
  height: 600px;
}

.service-container .bottom-shape span {
  position: absolute;
  /* border: 1px solid var(--primary); */
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  visibility: visible;
  display: block;
  top: 50%;
  left: 50%;
  opacity: 1;
}

/* ------------------------------------------ Shape ------------------------------------------*/

/* ------------------------------------------ Animation ------------------------------------------*/

@keyframes hoverEffectTop {
  0% {
    top: -150px;
    right: -70px;
    opacity: 0;
  }
  100% {
    top: -100px;
    right: -20px;
    opacity: 1;
  }
}

@keyframes hoverEffectBottom {
  0% {
    bottom: -150px;
    right: -70px;
    opacity: 0;
  }
  100% {
    bottom: -100px;
    right: -20px;
    opacity: 1;
  }
}

/* ------------------------------------------ Animation ------------------------------------------*/

@media (max-width: 480px) {
  .service-container .title {
    font-size: 22px;
  }
  .service-container ul li {
    column-gap: 1rem;
    font-size: 14px;
  }
  /* .service-container .icon-container svg{
        width: 100px;
        height: 60px;
    } */
  .service-container {
    padding: 70px 50px;
  }

  .service-container .top-shape .inner-most {
    width: 350px;
    height: 350px;
  }
  
  .service-container .top-shape .inner {
