.ant-back-top {
  right: 80px !important;
  bottom: 25px !important;
  background-color: var(--background) !important;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  width: max-content;
  height: max-content;
  border: 1px solid var(--secondary);
  border-radius: 50%;
  transition: all 0.3s ease;
}

.ant-back-top:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
}

/* .ant-back-top .scroll-top-btn {
  width: 100%;
  height: 100%;
} */

.ant-back-top svg {
  fill: var(--primary) !important;
  width: 35px;
  height: 35px;
}

@media (max-width: 600px) {
  .ant-back-top svg {
    width: 30px;
    height: 30px;
  }
}

@media (max-width: 500px) {
  .ant-back-top {
    right: 25px !important;
    bottom: 25px !important;
  }
  .ant-back-top svg {
    width: 20px;
    height: 20px;
  }
}
