.home-section-3-container {
  width: 100%;
  padding: 115px 10%;
  margin: 0 !important;
}

.home-section-3-container .left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 2rem;
  padding-top: 70px;
}

.home-section-3-container .left .top {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

.home-section-3-container .left .top .line-container {
  display: flex;
  align-items: center;
  column-gap: 0.3rem;
}

.home-section-3-container .left .top .line-container .line {
  height: 20px;
  width: 2px;
  background-color: var(--secondary);
}

.home-section-3-container .left .top .text-container {
  color: var(--secondary);
  font-size: 18px;
}

.home-section-3-container .left .middle {
  font-size: 60px;
  color: var(--primary);
  line-height: 80px;
  padding-bottom: 70px;
  font-family: Garamond-Bold !important;
}

.home-section-3-container .left .btn-container {
  background-color: var(--background);
  border: 1px solid var(--secondary);
  color: var(--primary);
  font-size: 16px;
  padding: 1.5rem 3rem;
  border-radius: 50px;
  transition: all 0.3s ease;
}

.home-section-3-container .left .btn-container:hover {
  cursor: pointer;
  background-color: var(--backgroundOne);
  border: 1px solid var(--backgroundOne);
}

.home-section-3-container .right {
  display: flex;
  flex-direction: column;
  row-gap: 1.5rem;
}

@media (min-width: 1600px) {
  .home-section-3-container {
    padding: 115px 20%;
  }
}

@media (max-width: 1199px) {
  .home-section-3-container .left {
    padding-top: 0 !important;
  }
  .home-section-3-container .left .middle {
    padding-bottom: 20px;
  }
}

@media (max-width: 992px) {
  .home-section-3-container {
    padding: 80px 10%;
  }
  .home-section-3-container .left .middle {
    font-size: 50px;
    line-height: 50px;
  }
}

@media (max-width: 768px) {
  .home-section-3-container .left .middle {
    font-size: 40px;
    line-height: 40px;
  }
}

@media (max-width: 575px) {
  .home-section-3-container .left .middle {
    font-size: 36px;
    line-height: 36px;
  }
}

