  .home-section-3-container .left .middle {
    font-size: 30px;
    line-height: 30px;
  }
} */

@media (max-width: 425px) {
  .home-section-3-container .left .btn-container {
    font-size: 14px;
    padding: 1rem 2.5rem;
    border-radius: 50px;
  }
}

@media(max-width : 375px){
  .home-section-3-container .left .btn-container {
    font-size: 13px;
    padding: 0.9rem 2.5rem;
  }
}
