.service-details-section-2-container {
  width: 100%;
  background-color: var(--backgroundOne);
  padding: 115px 10%;
}

.service-details-section-2-container .service-details-section-2-wrapper {
  width: 100%;
  margin: 0 !important;
}

.service-details-section-2-container .service-details-section-2-wrapper .col-1 {
  display: flex;
  flex-direction: column;
  row-gap: 4rem;
}

.service-details-section-2-container .service-details-section-2-wrapper .col-2 {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 3rem;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .banner-img {
  width: 100%;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .banner-img
  img {
  width: 100%;
  height: 100%;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .sec-1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 2rem;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .sec-2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 2rem;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .sec-3 {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 2rem;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .sec-3
  .plan-container {
  width: 100%;
    display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 2rem;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .sec-1
  .title {
  margin-bottom: 1rem;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .sec-2
  .content-2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 1.5rem;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .top {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .top
  .line-container {
  display: flex;
  align-items: center;
  column-gap: 0.3rem;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .top
  .line-container
  .line {
  height: 20px;
  width: 2px;
  background-color: var(--secondary);
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .top
  .text-container {
  color: var(--secondary);
  font-size: 18px;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .title {
  font-size: 66px;
  font-family: Garamond-Bold !important;
  color: var(--primary);
  line-height: 1.02;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .title-2 {
  font-size: 50px;
  font-family: Garamond-Bold !important;
  color: var(--primary);
  line-height: 1.02;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .content {
  font-size: 16px;
  color: var(--secondary);
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .content-2
  .text-container {
  font-size: 16px;
  color: var(--secondary);
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .content-2
  .content-2-middle {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .content-2
  .content-2-middle
  .content-2-middle-row {
  display: flex;
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .content-2
  .content-2-middle
  .content-2-middle-row
  .left {
  flex: 1;
  font-size: 28px;
  color: var(--primary);
}

.service-details-section-2-container
  .service-details-section-2-wrapper
  .col-2
  .content-2
  .content-2-middle
  .content-2-middle-row
  .right {
  flex: 2;
  font-size: 16px;
  color: var(--secondary);
  text-align: left;
}

@media (max-width: 1200px) {
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .title {
    font-size: 55px;
    line-height: 1.02;
  }
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .title-2 {
    font-size: 45px;
    line-height: 1.02;
  }
}

@media (max-width: 991px) {
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .title {
    font-size: 48px;
    line-height: 1.02;
  }
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .title-2 {
    font-size: 38px;
    line-height: 1.02;
  }
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .content-2
    .content-2-middle
    .content-2-middle-row
    .left {
    font-size: 26px;
  }
}

@media (max-width: 768px) {
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-1 {
    width: 100%;
    align-items: center;
  }
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .content-2
    .content-2-middle
    .content-2-middle-row {
    flex-direction: column;
    row-gap: 1rem;
  }
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .title {
    font-size: 38px;
  }
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .title-2 {
    font-size: 36px;
  }
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .content-2
    .content-2-middle
    .content-2-middle-row
    .left {
    font-size: 24px;
  }
}

@media (max-width: 575px) {
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .title {
    font-size: 34px;
    line-height: 1.02;
  }
  .service-details-section-2-container
    .service-details-section-2-wrapper
    .col-2
    .title-2 {
    font-size: 30px;
    line-height: 1.02;
  }
}

@media (max-width: 500px) {
  .service-details-section-2-container {
    padding: 80px 5%;
  }
}

@media (max-width: 380px) {
  .service-details-section-2-container {
    padding: 80px 2%;
  }
}
