.project-section-2-container {
  width: 100%;
  padding: 115px 10%;
  background-color: var(--backgroundOne);
}

.project-section-2-container .ant-tabs .ant-tabs-nav {
  margin: 0 0 30px 0 !important;
}

.project-section-2-container .ant-tabs .ant-tabs-nav::before {
  border: none !important;
}

.project-section-2-container .ant-tabs .ant-tabs-nav .ant-tabs-nav-wrap {
  justify-content: center !important;
}

.project-section-2-container
  .ant-tabs
  .ant-tabs-nav
  .ant-tabs-nav-wrap
  .ant-tabs-nav-list {
  justify-content: center !important;
  flex-wrap: wrap !important;
  column-gap: 3rem !important;
  row-gap: 2rem !important;
}

.project-section-2-container
  .ant-tabs
  .ant-tabs-nav
  .ant-tabs-nav-wrap
  .ant-tabs-nav-list
  .ant-tabs-ink-bar {
  display: none !important;
}

.project-section-2-container
  .ant-tabs
  .ant-tabs-nav
  .ant-tabs-nav-wrap
  .ant-tabs-nav-list
  .ant-tabs-tab {
  color: var(--primary) !important;
  border: 1px solid var(--secondary) !important;
  padding: 1rem 2rem !important;
  border-radius: 35px !important;
  transition: all 0.3s ease;
}

.project-section-2-container
  .ant-tabs
  .ant-tabs-nav
  .ant-tabs-nav-wrap
  .ant-tabs-nav-list
  .ant-tabs-tab:hover {
  background-color: var(--secondary) !important;
}

.project-section-2-container
  .ant-tabs
  .ant-tabs-nav
  .ant-tabs-nav-wrap
  .ant-tabs-nav-list
  .ant-tabs-tab.ant-tabs-tab-active {
  background-color: var(--secondary) !important;
}

.project-section-2-container
  .ant-tabs
  .ant-tabs-nav
  .ant-tabs-nav-wrap
  .ant-tabs-nav-list
  .ant-tabs-tab {
  margin: 0 !important;
}

.project-section-2-container
  .ant-tabs
  .ant-tabs-nav
  .ant-tabs-nav-wrap
  .ant-tabs-nav-list
  .ant-tabs-tab.ant-tabs-tab-active
  .ant-tabs-tab-btn {
  color: var(--primary) !important;
}

@media (max-width: 992px) {
  .project-section-2-container {
    padding: 75px 10%;
  }
}
